.Header_Left img{
    object-fit: contain;
    height: 50px;
    margin-left: 5px;
    background-color: transparent;
   

}
.Header{
    flex:1;
    margin-top: 0%;
    padding-top: 0%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    border: 1px solid whitesmoke;
    
    
   

}
.Header_Left{
    display: flex;
    align-items: center;
    flex:0.1;
   
}
.Header_Middle{
    display: flex;
    margin-left: 20px;
    background-color: lightgray;
    align-items: center;
    flex:1;
    padding-right: 0px;
   
    
}
.Header_Middle input {
    border: none;
    outline-width: 0px;
    height: 30px;
    flex:1;
    
    
    background-color: transparent;
   
   
}
.Header_Middle .MuiSvgIcon-root{
    padding: 0px;
    margin: 0px;
    width:20px;
    
    

}
.Header_Right{
    margin-left: 10px;
    flex:.1;
    display: flex;
    padding-right: 20px;
    align-items: center;
}